<template>
  <section class="order-wrapper">
    <card title="订单管理">
      <el-table :data="orderList" border>
        <el-table-column
          label="订单号"
          prop="oid"
          align="center"
        ></el-table-column>
        <el-table-column
          label="用户手机号"
          prop="tel"
          align="center"
          width="140"
        ></el-table-column>
        <el-table-column label="商品" align="center">
          <template slot-scope="scope">
            <span v-for="(item, index) in scope.row.opLinks" :key="index"
              >{{ index + 1 }}. {{ item.pName }}<br
            /></span>
          </template>
        </el-table-column>
        <el-table-column
          label="金额"
          prop="money"
          align="center"
          width="110"
          :formatter="priceFormatter"
        ></el-table-column>
        <el-table-column
          label="订单状态"
          prop="status"
          align="center"
          width="110"
        ></el-table-column>
        <el-table-column
          label="物流公司"
          prop="type"
          align="center"
        ></el-table-column>
        <el-table-column
          label="运单号"
          prop="waybill"
          align="center"
        ></el-table-column>
        <el-table-column label="操作" align="center" width="110">
          <template slot-scope="scope">
            <el-button size="mini" @click="viewDetails(scope.$index, scope.row)"
              >查看</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="PageSize"
        layout="total, prev, pager, next,jumper"
        :total="totalCount"
        style="margin: 20px 0 0 30px;"
      >
      </el-pagination>
    </card>
  </section>
</template>

<script>
import card from "../../components/card";

export default {
  components: {
    card
  },
  data() {
    return {
      // orderList: [
      //   {
      //     orderNum: "123456789",
      //     phone: "18812345678",
      //     commodity: ["A奶粉 X1", "B奶粉 X2"],
      //     price: 123,
      //     state: "已完成",
      //     waybillNum: "123456789"
      //   }
      // ],
      orderList: [],
      // 默认显示第几页
      currentPage: 1,
      // 默认每页显示的条数（可修改）
      PageSize: 10,
      totalCount: 0
    };
  },
  methods: {
    viewDetails(index, row) {
      this.$router.push({
        name: "checkorder",
        query: { uid: row.uid, oid: row.oid }
      });
    },
    priceFormatter(row, col) {
      if (Number(row[col.property]))
        return "￥" + Number(row[col.property]).toFixed(1);
      else return "/";
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
      this.getOrder();
    },
    getOrder() {
      this.$axios({
        url: "/back/order/order/getAllOrder",
        params: {
          page: this.currentPage
        }
      })
        .then(res => {
          if (res.data.code == 200) {
            this.PageSize = res.data.data.pageSize;
            this.totalCount = res.data.data.total;
            this.orderList = res.data.data.list;
          } else {
            this.$message({
              type: "error",
              message: "订单信息获取失败"
            });
          }
          setTimeout(() => {
            this.$openLoading().close();
          }, 200);
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: "订单信息获取失败"
          });
          setTimeout(() => {
            this.$openLoading().close();
          }, 200);
        });
    }
  },
  created() {
    this.$openLoading();
    this.getOrder();
  }
};
</script>

<style lang="less">
.order-wrapper {
  .card-btn {
    position: absolute;
    right: 20px;
    top: 6px;
  }
  .tag-wrapper {
    span {
      margin: 10px auto;
    }
  }
}
</style>
